function calc() {
    const result = document.querySelector('.calculating__result span')
    let sex, height, weight, age, ration;

    if (localStorage.getItem('sex')) {
        sex = localStorage.getItem('sex')
    } else {
        sex = 'male'
        localStorage.setItem('sex', 'male');
    }

    if (localStorage.getItem('ratio')) {
        ration = localStorage.getItem('ratio')
    } else {
        ration = 1.375
        localStorage.setItem('ratio', 1.375);
    }


    function iniLocalstorage(selector, activeClass) {
        let elements = document.querySelectorAll(selector)

        elements.forEach(el => {
            el.classList.remove(activeClass);
            if (el.getAttribute('id') == localStorage.getItem('sex'))
                el.classList.add(activeClass)
            if (el.getAttribute('data-ratio') == localStorage.getItem('ratio')) {
                el.classList.add(activeClass)
            }
        })
    }
    iniLocalstorage('#gender div', 'calculating__choose-item_active');
    iniLocalstorage('.calculating__choose_big div ', 'calculating__choose-item_active');

    function calcTotal() {
        if (!sex || !height || !weight || !age || !ration) {
            result.textContent = '______';
            return;
        }
        if (sex === 'female') {
            result.textContent = Math.round((447.6 + (9.2 * weight) + (3.1 * height) - (4.3 * age)) * ration);
        } else {
            result.textContent = Math.round((88.36 + (13.4 * weight) + (4.8 * height) - (5.7 * age)) * ration);
        }
    }

    calcTotal();

    function getStaticInformation(parentSelector, activeClass) {
        const elemets = document.querySelectorAll(`${parentSelector} div`);
        elemets.forEach(elem => {
            elem.addEventListener('click', (e) => {
                if (e.target.getAttribute('data-ratio')) {
                    ration = +e.target.getAttribute('data-ratio')
                    localStorage.setItem('ratio', ration);
                } else {
                    sex = e.target.getAttribute('id');
                    localStorage.setItem('sex', sex);
                }
                elemets.forEach(item => {
                    item.classList.remove(activeClass);
                })
                e.target.classList.add(activeClass)
                calcTotal();
            })
        })

    }
    getStaticInformation('#gender', 'calculating__choose-item_active');
    getStaticInformation('.calculating__choose_big', 'calculating__choose-item_active');

    function getDynamicInformation(selector) {
        const input = document.querySelector(selector);
        input.addEventListener('input', () => {
            if (input.value.match(/\D/g)) {
                input.style.border = ' 2px solid red'
            } else {
                input.style.border = ' 2px solid green'
            }
            switch (input.getAttribute('id')) {
                case 'height':
                    height = +input.value;
                    break;
                case 'weight':
                    weight = +input.value;
                    break;
                case 'age':
                    age = +input.value;
                    break;
            }
            calcTotal();
        })
    }

    getDynamicInformation('#height');
    getDynamicInformation('#weight');
    getDynamicInformation('#age');
}

export default calc;